import { createRouter, createWebHashHistory } from 'vue-router'
import Home from './views/Home.vue'
import Consejo from './views/Consejo.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/consejo',
    name: 'Consejo',
    component: Consejo
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
